<template>
  <div class="page-wrapper">
    <div class="cv-main">
      <div class="navbar-wrapper">
        <NavbarMain :navbarItems="navbarItemsArray" />
      </div>
      <router-view />
    </div>
  </div>
  <FooterMain :items="footerItems" />
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import NavbarMain from "@/components/NavbarMain.vue";
import FooterMain from "@/components/FooterMain.vue";

export default {
  components: {
    NavbarMain,
    FooterMain,
  },

  computed: {
    ...mapGetters(["selectedLanguage"]),

    selectedLanguage() {
      return this.$store.getters.selectedLanguage;
    },

    navbarItemsArray() {
      return this.$store.state.contentData[this.selectedLanguage].menuItems;
    },

    footerItems() {
      return this.$store.state.contentData.navbarItems;
    },
  },

  methods: {
    setCookieLanguage(lang) {
      if (lang === "de") {
        console.log("german");
        this.$store.commit("setLanguageDE");
      } else if (lang === "en") {
        this.$store.commit("setLanguageEN");
        console.log("english");
      }
    },

    ...mapMutations(["setLanguageDE", "setLanguageEN"]),
  },
};
</script>
